h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  margin: 0 0 1rem 0;
  font-weight: 400;
  line-height: 1.25;
  strong {
    font-weight: 800;
  }
}
h1, .h1 {
  font-size: 5.625rem;
  font-weight: 300;
}
h2, .h2 {
  font-size: 4rem;
  font-weight: 300;
}
h3, .h3 {
  font-size: 3.125rem;
  font-weight: 300;
}
h4, .h4 {
  font-size: 2.125rem;
}
h5, .h5 {
  font-size: 1.5rem;
}
h6, .h6 {
  font-size: 1.25rem;
  font-weight: 700;
}

@include media-breakpoint-down(xxl) {
  h1, .h1 {
    font-size: 3.875rem;
  }
  h2, .h2 {
    font-size: 2.625rem;
  }
  h3, .h3 {
    font-size: 2.625rem;
  }
}

@include media-breakpoint-down(lg) {
  h3, .h3 {
    font-size: 2.25rem;
  }
  h4, .h4 {
    font-size: 1.875rem;
  }
  h5, .h5 {
    font-size: 1.125rem;
  }
  h6, .h6 {
    font-size: 1.125rem;
    margin-bottom: 0.875rem;
  }
}

@include media-breakpoint-down(md) {
  h1, .h1 {
    font-size: 3rem;
  }
  h2, .h2 {
    font-size: 2.125rem;
  }
  h3, .h3 {
    font-size: 2.125rem;
  }
}
@include media-breakpoint-down(sm) {
  h1, .h1 {
    font-size: 2.625rem;
  }
  h2, .h2 {
    font-size: 1.75rem;
  }
  h3, .h3 {
    font-size: 1.75rem;
  }
  h4, .h4 {
    font-size: 1.5rem;
  }
}

p {
  margin: 0 0 1rem 0;
  &:last-child {
    margin-bottom: 0;
  }
  strong {
    font-weight: 600;
  }
  @include media-breakpoint-down(lg) {
    margin: 0 0 0.875rem 0;
  }
}

strong, b {
  font-weight: 700;
}

blockquote {
  padding: 20px 40px;
  background-color: var(--color-muted);
  margin: 1.25rem 0;
  &:last-child {
    margin-bottom: 0;
  }
  &:first-child {
    margin-top: 0;
  }
}

a {
  text-decoration: none;
  color: inherit;
}
a:not([class]), .link {
  transition: color $trs1, border-color $trs1;
  border-bottom: 1px solid currentColor;
  &[data-touch], &[data-hover] {
    color: #f94141;
    border-color: transparent;
  }
}